<template>
  <b-card :header="$t('Payment')">
    <b-button
      type="button"
      style="background-color: #ef402b;border-color: #C33421;color: white"
      @click="showLoader=false,getLanguage(),clearObjects(),$refs.addPaymentModel.show(),defaultSelection()"
      class="mr-1 mb-2 mt-xl-n2 pull-right"
      v-if="loginUser.Role=='M'"
    >{{$t('Add_Payment_Info')}}</b-button>
    <b-table
      :dark="dark"
      :sort-by.sync="sortBy"
      :hover="hover"
      :striped="striped"
      :bordered="bordered"
      :small="small"
      :fixed="fixed"
      responsive="lg"
      :items="gridData"
      :fields="captions"
      :current-page="currentPage"
      :per-page="perPage"
    >
      <template slot="actions" slot-scope="data" v-if="loginUser.Role=='M'">
        <a
          v-on:click="showLoader=false,clearObjects(),$refs.updatePaymentModel.show(), selectObject(data.item)"
          v-b-tooltip.hover
          :title="$t('Update')"
          class="icon mr-2 anchor-pointer"
          v-if="loginUser.Role=='M'"
        >
          <i id="editIcon" class="fa font-2x2 fa-pencil"></i>
        </a>
        <a
          v-on:click="$refs.deletePaymentInfoModel.show(),selectObject(data.item)"
          v-b-tooltip.hover
          :title="$t('Delete')"
          class="icon anchor-pointer"
          v-if="loginUser.Role=='M'"
        >
          <i class="cui-trash font-2x2 icons mt-1"></i>
        </a>
      </template>
    </b-table>
    <div class="pt-1 pb-4" v-if="gridData.length == 0 && role !== 'trador'">
      <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
      <div class="text-center">
        <b-spinner variant="primary" label="Text Centered"></b-spinner>
      </div>
    </div>
    <nav>
      <b-pagination
        class="pull-right"
        :total-rows="totalRows"
        :per-page="perPage"
        v-model="currentPage"
        prev-text="Prev"
        next-text="Next"
        hide-goto-end-buttons
      />
    </nav>

    <b-modal
      no-close-on-backdrop
      ref="deletePaymentInfoModel"
      :title="$t('Delete_Store')"
      class="modal-primary"
    >
      <p>
        {{$t('Are_you_sure_you_want_to_delete_payment_Details')}}
        <b style="color: #ef402b"></b> ?
      </p>
      <div slot="modal-footer" class="w-100">
        <b-button
          type="button"
          v-on:click="$refs.deletePaymentInfoModel.hide()"
          class="float-right ml-2"
        >
          <i class="fa fa-close"></i>
          {{$t('Cancel')}}
        </b-button>
        <b-button
          type="button"
          v-on:click="deletePaymentInfo($refs.deletePaymentInfoModel, deletePayment)"
          style="background-color: #ef402b;border-color: #C33421;color: white"
          class="float-right ml-2"
          variant="primary"
        >
          <i class="fa fa-dot-circle-o"></i>
          {{$t('Confirm')}}
        </b-button>
      </div>
    </b-modal>

    <b-modal
      no-close-on-backdrop
      ref="addPaymentModel"
      size="lg"
      hide-footer
      :title="$t('Add Payment Info')"
      class="modal-primary"
    >
      <b-form @submit.stop.prevent="showLoader=true,savePaymentInfo($refs.addPaymentModel, selectedPayment)">
        <b-row>
          <b-col md="6">
            <b-form-group :label="$t('Select_Store')" validated>
              <b-form-select v-model="selectedPayment.StoreID" required>
                <option :value="null" disabled>{{$t('Please_select_store')}}</option>
                <option
                  v-for="store in storeList"
                  :value="store.StoreID"
                  v-bind:key="store.StoreID"
                >{{store.StoreName}}</option>
              </b-form-select>
            </b-form-group>
          </b-col>

          <b-col md="6">
            <b-form-group validated :label="$t('IBAN')">
              <b-form-input
                type="text"
                v-model="selectedPayment.iban"
                :value="selectedPayment.iban"
                :placeholder="$t('Please_enter_iban')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('BIC')">
              <b-form-input
                type="text"
                v-model="selectedPayment.bic"
                :value="selectedPayment.bic"
                :placeholder="$t('Please_enter_bic')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('Account_Owner')">
              <b-form-input
                type="text"
                v-model="selectedPayment.account_owner"
                :value="selectedPayment.account_owner"
                :placeholder="$t('Please_enter_account_owner')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('Account  Addreess')">
              <b-form-input
                type="text"
                v-model="selectedPayment.account_address"
                :value="selectedPayment.account_address"
                :placeholder="$t('Please_enter_account_address')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button
            type="submit"
            class="ml-2"
            style="background-color: #ef402b;border-color: #C33421;color: white" v-if="showLoader==false"
          >
            <i class="fa fa-dot-circle-o"></i>
            {{$t('Save')}}
          </b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 

          <b-button
            type="button"
            class="ml-2"
            v-on:click="$refs.addPaymentModel.hide(),clearObjects()"
          >
            <i class="fa fa-close"></i>
            {{$t('Cancel')}}
          </b-button>
        </div>
      </b-form>
    </b-modal>

    <b-modal
      no-close-on-backdrop
      ref="updatePaymentModel"
      size="lg"
      hide-footer
      :title="$t('Add Payment Info')"
      class="modal-primary"
    >
      <b-form @submit.stop.prevent="showLoader=true,updatePaymentInfo($refs.updatePaymentModel, editPayment)">>
        <b-row>
          <b-col md="6">
            <b-form-group :label="$t('Select_Store')" validated>
              <b-form-select v-model="editPayment.StoreID">
                <option :value="null" disabled>{{$t('Please_select_store')}}</option>
                <option
                  v-for="store in storeList"
                  :value="store.StoreID"
                  v-bind:key="store.StoreID"
                >{{store.StoreName}}</option>
              </b-form-select>
            </b-form-group>
          </b-col>

          <b-col md="6">
            <b-form-group validated :label="$t('IBAN')">
              <b-form-input
                type="text"
                v-model="editPayment.iban"
                :value="editPayment.iban"
                :placeholder="$t('Please_enter_iban')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('BIC')">
              <b-form-input
                type="text"
                v-model="editPayment.bic"
                :value="editPayment.bic"
                :placeholder="$t('Please_enter_bic')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('Account Owner')">
              <b-form-input
                type="text"
                v-model="editPayment.account_owner"
                :value="editPayment.account_owner"
                disabled
                :placeholder="$t('Please_enter_account_owner')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('Account_Address')">
              <b-form-input
                type="text"
                v-model="editPayment.account_address"
                :value="editPayment.account_address"
                :placeholder="$t('Please_enter_account_address')"
                required
              ></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button
            type="submit"
            class="ml-2"
            style="background-color: #ef402b;border-color: #C33421;color: white" v-if="showLoader==false"
          >
            <i class="fa fa-dot-circle-o"></i>
            {{$t('Save')}}
          </b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="$refs.updatePaymentModel.hide()">
            <i class="fa fa-close"></i>
            {{$t('Cancel')}}
          </b-button>
        </div>
      </b-form>
    </b-modal>
  </b-card>
</template>

<script>
import VueNotifications from "vue-notifications";
import * as toutPaieService from "./ToutPaieService.js";

export default {
  props: {
    caption: {
      type: String,
      default: "Managers"
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    }
  },
  data: () => {
    return {
      paymentInfo: [],
      storeList: [],
      showLoader:false,
      selectedPayment: {
        StoreID: null,
        storename: null,
        iban: null,
        bic: null,
        account_owner: null,
        account_address: null
      },
      sortBy: "StoreName",
      pageOptions: [5, 10, 15],
      currentPage: 1,
      perPage: 20,
      file: null,
      images: [1, 2, 3, 4],
      role: "admin",

      editPayment: {
        IsActive: false
      },

      deletePayment: {}
    };
  },
  computed: {
    fields() {
      return [
        {
          key: "StoreName",
          label: this.translateString("Store_Name"),
          sortable: true
        },
        { key: "IBAN", label: this.translateString("IBAN"), sortable: true },

        { key: "BIC", label: this.translateString("BIC"), sortable: true },

        {
          key: "AccountOwner",
          label: this.translateString("Account_Owner"),
          sortable: true
        },
        {
          key: "AccountAddress",
          label: this.translateString("Account_Address"),
          sortable: true
        },
        {
          key: "actions",
          label: this.translateString("Action"),
          sortable: false,
          thStyle: { width: "80px !important", "min-width": "80px !important" }
        }
      ];
    },
    gridData: function() {
      return this.paymentInfo;
    },
    totalRows: function() {
      return this.getRowCount();
    },
    captions: function() {
      return this.fields;
    }
  },
  methods: {
    translateString: function(colName) {
      return this.$i18n.t(colName);
    },
    getLanguage: function() {
      this.language = window.localStorage.getItem("language");
    },

    clearObjects: function() {
      this.selectedPayment = {};
      this.editPayment = {};
      this.deletePayment = {};
      
    },

    selectObject: function(payment) {
      // console.log(payment);
      this.editPayment = payment;
      this.editPayment.PaymentInformationId = payment.PaymentInformationId;
      this.editPayment.storeID = payment.storeID;
      this.editPayment.iban = payment.IBAN;
      this.editPayment.bic = payment.BIC;
      this.editPayment.account_owner = payment.AccountOwner;
      this.editPayment.account_address = payment.AccountAddress;
      this.deletePayment = payment;
      // this.deletePayment.PaymentInformationId = payment.PaymentInformationId;
      // console.log("DeleteData", this.deletePayment.PaymentInformationId);
      // console.log("Edit Data", this.editPayment);
    },

    checkValidation: function(modal) {
      if (this.editUser) {
        this.selectedPayment = this.editPayment;
      } else {
        this.selectedPayment = this.selectedPayment;
      }
      if (
        !(
          this.selectedPayment &&
          this.selectedPayment.StoreID &&
          this.selectedPayment.iban &&
          this.selectedPayment.bic &&
          this.selectedPayment.account_owner &&
          this.selectedPayment.account_address
        )
      ) {
        VueNotifications.error({
          message: "Please fill all mandatory fields of Store Info Tab.",
          timeout: 2500
        });
        setTimeout(() => {
          this.tabIndex = 0;
          this.edittabIndex = 0;
        }, 0);
      } else {
        modal.hide();
        // VueNotifications.success({message: 'Data Saves Successfully',timeout:2500});
        // this.selectedStore = {};
      }
    },

    savePaymentInfo: function(ref, selectedPayment) {

      this.loginUser = JSON.parse(window.localStorage.getItem("loginUser"));
      let paymentData = selectedPayment;
      let paymentForm = new FormData();

      paymentForm.append("StoreID", paymentData.StoreID);
      paymentForm.append("IBAN", paymentData.iban);
      paymentForm.append("BIC", paymentData.bic);
      paymentForm.append("AccountOwner", paymentData.account_owner);
      paymentForm.append("AccountAddress", paymentData.account_address);
      paymentForm.append("AddedBy", this.loginUser.UserID);

      let url = null;
      if (this.editUser === true && paymentData.PaymentInformationId) {
        url = "payment/update/" + paymentData.PaymentInformationId;
        this.editUser = false;
      } else {
        url = "payment/save";
      }

      toutPaieService.saveData(paymentForm, url).then(data => {
        // console.log("SAVEdata", this.paymentForm);
        let msg = data.data[0].message;

        if (data.status == 200 && msg == "Success") {
          ref.hide();
          this.showLoader=false;
          this.clearObjects();
          this.getPaymentIf();
          VueNotifications.success({
            message: "Payment Info Added Sucessfully",
            timeout: 2500
          });
          
        } else {
           this.showLoader=false;
          VueNotifications.error({ message: msg, timeout: 2500 });
        }
      });
    },

    updatePaymentInfo: function(ref, payment) {
      this.editUser = true;
      this.savePaymentInfo(ref, payment);
    },

    deletePaymentInfo: function(refs, deletePayment) {
      let URL = "payment/delete/" + deletePayment.PaymentInformationId;
      // console.log("Deleted ID", this.deletePayment.PaymentInformationId);

      toutPaieService.getData(URL).then(res => {
        let msg = res.status;
        //console.log("Message",msg)
        if (msg == "Success") {
          refs.hide();
          VueNotifications.success({
            message: "Payment Info Deleted  Successfully  ",
            timeout: 2500
          });
          this.clearObjects();
          this.getPaymentIf();
        } else {
          VueNotifications.error({
            message: "Payment Info Not Deleted",
            timeout: 2500
          });
          refs.hide();
        }
      });
    },

    getRowCount: function() {
      return this.paymentInfo.length;  
    },

    defaultSelection: function(){
       
               if(this.loginUser.Role=="M"){
          if(this.storeList.length>0){
            for(var i=0;i<this.storeList.length;i++){

          this.selectedPayment.StoreID=this.storeList[i].StoreID;
           }
               
               
          }
       }
    },
     

    getStores: function() {
      this.loginUser = JSON.parse(window.localStorage.getItem("loginUser"));

      let url = null;
      if (this.loginUser.Role == "M") {
        url = "user/store/get/" + this.loginUser.UserID;
      } else {
        url = "store";
      }

      toutPaieService.getData(url).then(res => {
        this.storeList = res.data;
      });
    },


    getPaymentIf: function() {
      this.loginUser = JSON.parse(window.localStorage.getItem("loginUser"));
      let url = null;
      if (this.loginUser.Role == "M") {
        url = "user/payment/info/" + this.loginUser.UserID;
      } else {
        url = "payments";
      }
         
      toutPaieService.getData(url).then(res => {
        this.paymentInfo = res.data;
        // console.log("Response Data", res.data);
      });

      //console.log("Payment Info",this.paymentInfo);
    }
  },
  beforeMount() {
    this.getPaymentIf();
    this.getStores();
  }
};
</script>
